import React from 'react';
import {Link} from "react-router-dom";
import './Menu.scss';
import {Transition} from "react-transition-group";
import ClickAwayListener from '@material-ui/core/ClickAwayListener';

import logoIcon from '../../img/logo.svg';
import phoneIcon from '../../img/phone.svg';
import panoramaIcon from '../../img/panorama.svg';

const Menu = ({toggleMenu, visibleMenu}) => {
    return (
        <Transition
            timeout={1240}
            mountOnEnter={true}
            unmountOnExit={true}
            in={visibleMenu}
        >
            <ClickAwayListener onClickAway={toggleMenu}>
                <div className="menu">
                    <div className="menu__close" onClick={toggleMenu}>
                        <span className="icon-times" />
                    </div>
                    <div className="menu__container">
                        <div className="container">
                            <div className="menu__content">
                                <div className="menu__logo">
                                    <Link className="logo" to="/kvartalgeroev">
                                        <img src={logoIcon} alt="logo" />
                                    </Link>
                                </div>
                                <nav className="menu__nav">
                                    <div className="menu__item">
                                        <div className="menu__head">
                                            <div className="menu__head__item menu__toggle">
                                                <Link to="/about"> </Link>
                                            </div>
                                        </div>
                                        <div className="menu__links">
                                            <div className="menu__links__item">
                                                <Link to="/about/infra"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/about/gallery"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/about/aerial"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/about/video"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/about/building"> </Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/about/docs"></Link>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="menu__item">
                                        <div className="menu__head">
                                            <div className="menu__head__item menu__toggle">
                                                <Link to="/apartments/params"> </Link>
                                            </div>
                                        </div>
                                        <div className="menu__links">
                                            <div className="menu__links__item">
                                                <Link to="./kvartalgeroev"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/apartments">  </Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="./kvartalgeroev"></Link>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="menu__item">
                                        <div className="menu__head">
                                            <div className="menu__head__item menu__toggle">
                                                <Link to="/buy/mortgage/"> </Link>
                                            </div>
                                        </div>
                                        <div className="menu__links">
                                            <div className="menu__links__item">
                                                <Link to="/buy/mortgage/"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/buy/military/"> </Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/buy/family/"> </Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/buy/maternal/"> </Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/buy/installment/"></Link>
                                            </div>
                                            <div className="menu__links__item">
                                                <Link to="/buy/netting/"> </Link>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="menu__item">
                                        <div className="menu__head">
                                            <div className="menu__head__item">
                                                <Link to="/stock"></Link>
                                            </div>
                                            <div className="menu__head__item">
                                                <Link to="/news"></Link>
                                            </div>
                                            <div className="menu__head__item">
                                                <Link to="/contacts"></Link>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="menu__item">
                                        <div className="links-list">
                                            <div className="links-list__item">
                                                <a className="link" href="tel:+7(495)737-07-77">
                                                    <span className="link__text">+7 (495) 737-07-77</span>
                                                </a>
                                            </div>
                                            <div className="links-list__item links-list__item_callback">
                                                <a className="link open-popup" href="#callback-popup">
                                            <span className="link__icon">
                                                <img src={phoneIcon} alt="phone"/>
                                            </span>
                                                    <span className="link__text"> </span>
                                                </a>
                                            </div>
                                            <div className="links-list__item">
                                                <a className="link open-popup" href="#panorama-popup">
                                            <span className="link__icon">
                                                <img src={panoramaIcon} alt="" />
                                            </span>
                                                    <span className="link__text"></span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ClickAwayListener>

        </Transition>
    );
};

export default Menu;