import React from 'react';
import Benefits from "../Benefits";

const BenefitsSection = () => {
    const items = [
        {
            id: 1,
            img: './img/1.svg',
            text: ` <br />  `,
        },
        {
            id: 2,
            img: './img/2.svg',
            text: `   <br>   -`,
        },
        {
            id: 3,
            img: './img/3.svg',
            text: `C `,
        },
        {
            id: 4,
            img: './img/4.svg',
            text: `  <br>  `,
        },
        {
            id: 5,
            img: './img/5.svg',
            text: ``,
        }
    ];

    return (
        <div className="section">
            <div className="container">
                <Benefits items={items}/>
            </div>
        </div>
    );
};

export default BenefitsSection;